<template>
    <b-form class="p-4 m-2" @submit="onSubmit" @reset="onReset" v-if="show">
        <h3 class="text-center text-dark">Registro de teléfono</h3>
        <b-form-group label="Modelo: ">
            <b-form-input type="text" v-model="form.modelo" placeholder="Ingrese el modelo" required></b-form-input>
        </b-form-group>
        <b-form-group label="Número de serie: ">
            <b-form-input type="text" v-model="form.serie" placeholder="Ingrese el # de serie" required></b-form-input>
        </b-form-group>
        <b-form-group label="EMEI: ">
            <b-form-input type="text" v-model="form.emie" placeholder="Ingrese el EMEI" required></b-form-input>
        </b-form-group>
        <b-form-group label="Accesorios:">
            <b-form-checkbox-group v-model="form.accesorios">
                <b-form-checkbox value="Cable">Cable</b-form-checkbox>
                <b-form-checkbox value="Eliminador de corriente">Eliminador de corriente</b-form-checkbox>
                <b-form-checkbox value="Funda">Funda</b-form-checkbox>
            </b-form-checkbox-group>
        </b-form-group>

        <b-form-group label="Status: ">
            <b-form-select v-model="form.status" :options="options_status"></b-form-select>
        </b-form-group>
        <div class="text-right">
            <b-button-group>
                <router-link to="/"  class="text-light"><b-button  type="reset" variant="danger">Cancelar</b-button></router-link>
                <b-button type="submit" variant="primary">Guardar</b-button>
            </b-button-group>
        </div>
        <b-card class="mt-3" header="Form Data Result">
            <pre class="m-0">{{ form }}</pre>
        </b-card>
    </b-form>
</template>
<script>
export default {

    data(){
        return{
            form: {
                modelo: '',
                serie: '',
                status: null,
                accesorios : []
            },
            show: true,
            options_status : [
                {value: "Asignado", text: "Asignado"},
                {value: "Sin asignar", text: "Sin asignar"}
            ],
        }
    },
    methods:{
        onSubmit(evt) {
            evt.preventDefault();
            alert(JSON.stringify(this.form));
        },
        onReset(evt) {
            evt.preventDefault();
            // Reset our form values
            this.form.user = '';
            // Trick to reset/clear native browser form validation state
            this.show = false
            this.$nextTick(() => {
            this.show = true
            });
        }
    }
}
</script>
